*{
  margin: 0;
  padding: 0;
  list-style: none;
  outline: none;
  font-family: 'Roboto', sistem-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI',  'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}
.todo {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 750px;
  height: 530px;
  transform: translate(-50%, -50%);
  box-shadow: 1px 2px 20px #f3f3f3;
  border-radius: 10px;
  border: 1px solid #f1f1f1;

  &__sidebar {
    background-color: #f4f6f8;
    width: 200px;
    height: 100%;
    border-right: 1px solid #f1f1f1;
    padding: 60px 20px;
  }

  &__list {
    li {
      display: flex;
      align-items: center;
      padding: 10px 12px;
      cursor: pointer;
      margin-bottom: 5px;

      &.active {
        background: #FFFFFF;
        box-shadow: 0px 2px 10px rgba(0, 3, 0, 0.04);
        border-radius: 4px;
      }


      i {
        display: inline-flex;
        margin-right: 10px;
        img{
          opacity: 0.6;
        }
      }
    }
  }

}



